import { Component, OnInit } from '@angular/core';
import {SharedService} from 'src/app/shared.service';

@Component({
  selector: 'app-show-emp',
  templateUrl: './show-emp.component.html',
  styleUrls: ['./show-emp.component.css']
})
export class ShowEmpComponent implements OnInit {

  constructor(private service:SharedService) { }
public empList:any=[];

  
ModelTitle:string;
ActivateAddEditempComp:boolean=false;
//dep:any;
 emp:any;


  ngOnInit(): void {
    console.log(1);
   this.refreshempList();
   
  }

  addClick(){
    this.emp={
      employeeId:0,
      employeeName:"",
      department:"",
      dateOfJoining:""
    }
    this.ModelTitle="Add Employee";
    this.ActivateAddEditempComp=true;
  }

  closeClick(){
    this.ActivateAddEditempComp=false;
    this.refreshempList();
  }

  editClick(item){

 this.emp=item;
 console.log('emp details '+this.emp.employeeName);

  console.log('emp details '+this.emp.dateOfJoining);
this.ModelTitle="Edit Department";
this.ActivateAddEditempComp=true;
  }

  deleteClick(item){
    if(confirm('Are you sure??')){
          this.service.deleteEmployee(item.employeeId).subscribe(
        data=>{alert(data.toString());
        this.refreshempList();
        })
    }
  }
    refreshempList(){
    this.service.getEmpList().subscribe(data=>{
      this.empList=data;
        // console.log(this.empList);
      // console.log(data);
      // console.log(this.DepartmentList);
    });
  }
}
