import { Component, OnInit } from '@angular/core';
import {SharedService} from 'src/app/shared.service';

@Component({
  selector: 'app-show-dept',
  templateUrl: './show-dept.component.html',
  styleUrls: ['./show-dept.component.css']
})
export class ShowDeptComponent implements OnInit {

  constructor(private service:SharedService) { }
public DepartmentList:any=[];

public name:string='pankajsingh';
public nameList:any=[];
public  HEROES = [
  {id: 1, name:'Superman'},
  {id: 2, name:'Batman'},
  {id: 5, name:'BatGirl'},
  {id: 3, name:'Robin'},
  {id: 4, name:'Flash'}
];

ModelTitle:string;
ActivateAddEditDepComp:boolean=false;
//dep:any;
 dep:any;


 DepartmentIdFilter:string="";
 DepartmentNameFilter:string="";
DepartmentListWithoutFilter:any=[];

  ngOnInit(): void {
   this.refreshDepList();
  }

  addClick(){
    this.dep={
      departmentId:0,
      departmentName:""
    }
    this.ModelTitle="Add Department";
    this.ActivateAddEditDepComp=true;
  }

  addMulClick(){
    this.dep={
      departmentId:0,
      departmentName:""
    }
    this.ModelTitle="Add Multiple Department";
    this.ActivateAddEditDepComp=true;
  }

  closeClick(){
    this.ActivateAddEditDepComp=false;
    this.refreshDepList();
  }

  editClick(item){
 this.dep=item;
 console.log(this.dep);
this.ModelTitle="Edit Department";
this.ActivateAddEditDepComp=true;
  }

  deleteClick(item){
    if(confirm('Are you sure??')){
          this.service.deleteDepartment(item.departmentId).subscribe(
        data=>{alert(data.toString());
        this.refreshDepList();
        })
    }
  }
    refreshDepList(){
    this.service.getDepList().subscribe(data=>{
      this.DepartmentList=data;
      this.DepartmentListWithoutFilter=data;
    });
  }

  FilterFn(){
var DepartmentIdFilter=this.DepartmentIdFilter;
var DepartmentNameFilter=this.DepartmentNameFilter;

this.DepartmentList=this.DepartmentListWithoutFilter.filter(function(el){
return el.departmentId.toString().toLowerCase().includes(
  DepartmentIdFilter.toString().trim().toLocaleLowerCase()
) && 
el.departmentName.toString().toLowerCase().includes(
  DepartmentNameFilter.toString().trim().toLocaleLowerCase()
)

});

  }

  sortResult(prop,asc){
this.DepartmentList=this.DepartmentListWithoutFilter.sort(function(a,b){
  if(asc){
    return (a[prop]>b[prop]?1:(a[prop]<b[prop]?-1:0));
  }
  else  {
    return (b[prop]>a[prop]?1:(b[prop]<a[prop]?-1:0));

  }
})

}

}
